  color: #191970;
}
a:focus {
  outline: thin dotted;
}
a:hover, a:active {
  outline: 0;
}

/* Forms
--------------------------------------------- */
.button,
button,
input[type=button],
input[type=reset],
input[type=submit] {
  display: inline-block;
  border: 1px solid;
  border-color: #ccc #ccc #bbb;
  border-radius: 3px;
  background: #e6e6e6;
  color: rgba(0, 0, 0, 0.8);
  line-height: 1;
  cursor: pointer;
  padding: 0.625rem 1.25rem;
}
.button:hover,
button:hover,
